.especialidades-container {
    font-family: "Poppins", serif;
    margin: 0;
    padding: 0;
    color: #333;
    line-height: 1.6;
  }
  
  .h1-especialidades {
    font-family: "Poppins", serif;
    font-weight: 500;
    font-style: normal;
    font-size: 24px;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 30px;
  }
  
  /* p {
    font-weight: 300;
    font-size: 20px;
    margin: 10px 0;
    text-align: justify;
  } */
  

  .chef-section {
    padding: 50px;
    background-color: #f5f5f5;
  }
  
  .chef-content {
    display: flex;
    gap: 30px;
    align-items: center;
    justify-content: center;
    padding: 70px;
    padding-top: 30px;
  }
  
  .chef-image img {
    width: 100%;
    width: 500px;

  }
  
  .chef-description {
    max-width: 600px;
    padding: 20px;
  }
  
  .picks-section {
    padding: 10px 20px;
    background-color: #E5EAE6;
  }
  
  .picks-description {
    text-align: center;
    max-width: 800px;
    margin: 0 auto 40px;
    font-size: 14px;
  }
  
  .picks-content {
    display: flex;
    flex-direction: column;
    gap: 30px;
    padding: 0 20px;
    margin-bottom: 50px;
    
  }
  
  .pick-item {
    display: flex;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
    font-size: 20px;
  }
  
  .pick-item img {
    width: 70%;
    height: auto;
    
  }
  
  .pick-item.left {
    flex-direction: row;
  }
  
  .pick-item.right {
    flex-direction: row-reverse;
  }
  

  /* Responsividade */

/* Smartphones pequenos (Portrait: até 693px) */
@media (max-width: 693px) {
  .chef-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .chef-image img {
    max-width: 100%;
    padding-top: 5px;
    padding-bottom: 5px;
  }

  .chef-description {
    margin: 10px;
    font-size: 10px;
    text-align: justify;
    padding: 10px;
  }
  
  .picks-description {
    text-align: justify;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 10px;
  }

  .picks-content {
    display: flex;
    flex-direction: column; 
    align-items: center; 
    gap: 15px; 
    padding: 0; 
    margin: 0 auto; 
  }

  .pick-item.right img {
    width: 100%; 
    height: auto; 
    margin-bottom: 2px;
  }

  .pick-item.left img {
    width: 100%; 
    height: auto; 
    margin-bottom: 2px;
  }

  .pick-item.left {
    flex-direction: column; 
    align-items: center; 
    margin-left: 10px;
    margin-right: 10px;
    font-size: 12px;
  }

  .pick-item.right {
    flex-direction: column; 
    align-items: center; 
    margin-left: 10px;
    margin-right: 10px;
    font-size: 12px;
  }
}

