.bannerStyle {
    bottom: 0;
    width: 100%;
    background-color: #e6e5e5;
    font-family: "Poppins", serif;
    display: flex;
    font-weight: 500;
    font-style: normal;
}

.contentStyle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.contentStyle p {
    margin: 0;
    padding: 10px;
    font-size: 12px;
}

.buttonStyle {
    margin-left: 20px;
    background-color: #4CAF50;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 4px;
    font-size: 14px;
}

@media (max-width: 769px) {
    .bannerStyle {
        position: fixed;
    }
}