/* home.css */


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* .imghome {
  width: 100%;
  height: auto;
  margin-bottom: 0px;
  padding-bottom: 0px;
  display: block;
} */

.blocohome-h img {
  width: 100%;
  height: auto;
  margin-bottom: 0px;
  padding-bottom: 0px;
  display: block;
}

.blocohome-v img {
  width: 100%;
  height: auto;
  margin-bottom: 0px;
  padding-bottom: 0px;
  display: block;
}

.blocohome-h {
  display: block;
}

.blocohome-v {
  display: none;
}

@media (max-width: 768px) {
  .blocohome-h {
    display: none;
  }

  .blocohome-v {
    display: block;
  }

  .blocohome-v img {
    width: 100%;
    height: auto;
    margin-bottom: 0px;
    padding-bottom: 0px;
    display: block;
  }
}

@media (min-width: 769px) {
  .blocohome-h {
    display: block;
  }

  .blocohome-v {
    display: none;
  }

  .blocohome-v img {
    width: 100%;
    height: auto;
    margin-bottom: 0px;
    padding-bottom: 0px;
    display: block;
  }
}