.h1-sobre {
    font-family: "Poppins", serif;
    font-weight: 500;
    font-style: normal;
    font-size: 24px;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 30px;
}

.containersobre {
    display: flex;
    padding: 80px;
    margin: 20px;
    gap: 5px;
    align-items: center;
}

.imgsobre {
    width: 100%;
    height: auto;
}

.psobre {
    font-family: "Poppins", serif;
    font-weight: 300;
    font-style: normal;
    font-size: 13px;
    text-align: justify;
    padding: 40px;
}

.texto,
.imagem {
    flex: 1;
}


/* Responsividade */

@media (max-width: 480px) {
    .containersobre {
        display: none;
    }

    .containersobre-responsividade {
        flex-direction: column;
        padding: 20px;
        gap: 20px;
    }

    .psobre {
        font-size: 12px;
        padding: 20px;
    }

    .imgsobre {
        width: 100%;
        margin-top: 0px;
    }
}

/* Smartphones pequenos (até 768px) */
@media (min-width: 481px) and (max-width: 768px) {
    .containersobre {
        display: none;
    }

    .containersobre-responsividade {
        flex-direction: column;
        padding: 20px;
        gap: 20px;
    }

    .psobre {
        font-size: 12px;
        padding: 20px;
    }

    .imgsobre {
        width: 100%;
        margin-top: 20px;
    }
}


/* Tablets e laptops pequenos (769px a 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
    .h1-sobre {
        margin-top: 50px;
        margin-bottom: 50px;
    }

    .containersobre {
        display: none;
    }

    .containersobre-responsividade {
        flex-direction: column;
        padding: 30px;
        padding-top: 10px;

    }

    .psobre {
        font-size: 16px;
        margin: 20px;
    }

    .imgsobre {
        width: 100%;
    }
}

/* Laptops grandes e desktops pequenos (1025px a 1366px) */
@media (min-width: 1025px) and (max-width: 1366px) {
    .h1-sobre {
        margin-top: 60px;
        margin-bottom: 50px;
    }

    .containersobre {
        display: none;
    }

    .psobre {
        font-size: 22px;
        padding: 60px;
    }

    .imgsobre {
        width: 100%;
        padding: 30px;
        padding-bottom: 10px;
    }
}

/* Desktops grandes (1367px ou mais) */
@media (min-width: 1367px) {
    .h1-sobre {
        margin-top: 60px;
        margin-bottom: 50px;
    }

    .containersobre-responsividade {
        display: none;
    }
    .containersobre {
        flex-direction: row;
        padding: 80px;
        gap: 40px;
    }

    .psobre {
        font-size: 15px;
        padding: 40px;
    }

    .imgsobre {
        width: 100%;
    }
}