.footer {
    height: 150px;
    background-color: #F2F2F2;
    padding: 20px;
    padding-top: 3px;
    margin-top: 0px;
    font-family: "Poppins", serif;
    font-weight: 200;
    font-style: normal;
    display: flex;
    justify-content: space-between; 
    align-items: center; 
}

.footer-content {
    margin-top: 30px;
    padding-top: 0px;
    padding-bottom: 20px;
}

.footer-content-span {
    font-size: 10px;
    text-align: start;
    margin-bottom: 0px;
    margin-top: 0px; 
}

.pfooter{
    font-size: 15px;
    text-align: start;
}

.redessociais {
    display: flex;
    gap: 10px; 
    margin-top: 70px;
    padding-top: 0px;
    padding: 15px;
}

.imgredessociais {
    width: 20px;
}
 

/* Responsividade */

/* Smartphones pequenos (até 768px) */
@media (max-width: 768px) {
    .footer {
        height: auto;
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .footer-content {
        margin-top: 10px;
        padding-bottom: 10px;
    }

    .pfooter {
        font-size: 13px;
    }

    .footer-content-span {
        font-size: 10px;
    }

    .redessociais {
        margin-top: 10px;
        gap: 10px;
        padding: 10px 0;
        
    }

    .imgredessociais {
        width: 18px;
    }
}

/* Tablets e laptops pequenos (769px a 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
    .footer { 
        height: auto;
        flex-direction: row;
        align-items: center;
        padding: 20px;
    }

    .footer-content {
        margin-top: 20px;
    }

    .pfooter {
        font-size: 15px;
    }

    .footer-content-span {
        font-size: 12px;
    }

    .redessociais {
        margin-top: 0;
        justify-content: center;
    }

    .imgredessociais {
        width: 20px;
    }
}

/* Laptops grandes e desktops pequenos (1025px a 1366px) */
@media (min-width: 1025px) and (max-width: 1366px) {
    .footer {
        height: 150px;
        flex-direction: row;
        align-items: center;
        padding: 20px;
    }

    .footer-content {
        margin-top: 30px;
    }

    .pfooter {
        font-size: 15px;
    }

    .footer-content-span {
        font-size: 14px;
    }

    .redessociais {
        margin-top: 0;
        justify-content: center;
    }

    .imgredessociais {
        width: 30px;
    }
}

/* Desktops grandes (1367px ou mais) */
@media (min-width: 1367px) {
    .footer {
        height: 150px;
        flex-direction: row;
        align-items: center;
        padding: 20px;
    }

    .footer-content {
        margin-top: 30px;
    }

    .pfooter {
        font-size: 15px;
    }

    .footer-content-span {
        font-size: 18px;
    }

    .redessociais {
        margin-top: 0;
        justify-content: flex-end;
    }

    .imgredessociais {
        width: 40px;
    }
}
