/* NAVBAR CSS */

/* fonte importada do google */
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

nav {
  height: 140px;
  background-color: #F2F2F2;
  padding: 20px;
  font-family: "Poppins", serif;
  font-weight: 500;
  font-style: normal;
  text-align: center;
}

.menu-hamburguer {
  display: none;
}

.titleprincipal {
  margin: auto;
  font-family: "Poppins", serif;
  font-weight: 500;
  font-style: normal;
  font-size: 24px;
  text-align: center;
  padding: 15px;
  padding-top: 10px;
  margin-bottom: 20px;
  position: relative;
  display: inline-block;
  padding-bottom: 10px;
}

.titleprincipal::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 200%;
  height: 0.9px;
  background-color: #333;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
  font-size: 10px;
  padding-top: 5px;
}

nav ul li {
  margin: 0 45px;
}

nav ul li a {
  color: #333;
  text-decoration: none;
}

nav ul li a:hover {
  text-decoration-line: underline;
}

nav ul li a.menu-ativo {
  font-weight: bold;
  color: #ff6347;
  text-decoration: underline;
}


/* Responsividade */

/* Smartphones pequenos (até 768px) */
@media (max-width: 768px) {
  nav {
    height: auto;
    padding: 20px;

  }

  .titleprincipal {
    font-size: 16px;
    padding: 10px 0;
  }

  nav ul {
    display: none;
  }

  .menu-hamburguer {
    display: block;
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 24px;
    cursor: pointer;
  }

  .menu-hamburguer.ativo+ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #F2F2F2;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    padding: 10px 0;
    z-index: 1000;
  }

  nav ul li {
    margin: 10px 0;
  }
}


/* Tablets e laptops pequenos (769px a 940px) */
@media (min-width: 769px) and (max-width: 940px) {
  nav {
    height: 160px;

  }


}

/* Laptops grandes e desktops pequenos (1025px a 1366px) */
@media (min-width: 1025px) and (max-width: 1366px) {
  nav {
    height: 160px;
    padding: 20px;

  }

  .titleprincipal {
    font-size: 24px;
  }

  nav ul {
    font-size: 14px;

  }

  nav ul li {
    margin: 0 45px;

  }
}

/* Desktops grandes (1367px ou mais) */
@media (min-width: 1367px) {
  nav {
    height: 140px;
    padding: 20px;
  }

  .titleprincipal {
    font-size: 24px;
  }

  nav ul {
    font-size: 14px;
  }

  nav ul li {
    margin: 0 50px;
  }
}