/* Smartphones pequenos (Portrait: até 768px) */
@media (max-width: 768px) {
  .h1-menu {
    margin-bottom: 25px;
  }

  .p-menu {
    font-size: 14px;
    margin-left: 40px;
    margin-right: 40px;
    text-align: justify;
  }

  .grid-menu,
  .grid-takeaway {
    padding: 20px;
  }

  .card-prato {
    font-size: 10px;
  }

