.img-menugrupo {
  width: 100%;
  height: auto;
  margin: auto;
}

.menugrupo-description p {
  font-size: 18px;
  font-family: "Poppins", serif;
  font-weight: 200;
  font-style: normal;
}

.menugrupo-description {
  padding: 200px;
  padding-top: 20px;
  margin: auto;
  padding-bottom: 40px;
  text-align: justify;
}

.menugrupo-h1 {
  font-family: "Poppins", serif;
  font-weight: 500;
  font-style: normal;
  font-size: 24px;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 30px;
}

.menu-titulo {
  text-align: center;
  margin-bottom: 40px;
  padding: 100px;
  padding-top: 20px;
  padding-bottom: 3px;
  margin: auto;
}

.menu-titulo-p {
  font-size: 16px;
  font-family: "Poppins", serif;
  font-weight: 200;
  font-style: normal;
}

.menu-titulo-button {
  text-align: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  padding: 20px;
  background-color: #F26938;
  color: white;
  border: none;
  border-radius: 8px;
  margin-top: 20px;
  margin-bottom: 40px;
  cursor: pointer;
}

.container {
  display: flex;
  flex-direction: column;
}

.container-drop {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: nowrap;
  margin-top: 20px;
  margin-bottom: 40px;
  margin-left: 20px;
  margin-right: 20px;
  padding: 20px;
}

.menu-titulo_bloco2 {
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 40px;
}

.bloco2-p {
  font-size: 16px;
  font-family: "Poppins", serif;
  font-weight: 200;
  font-style: normal;
  text-align: center;
  margin-bottom: 20px;
}

.dropdown {
  position: relative;
}

.dropdown-button {
  padding: 30px 50px;
  font-size: 16px;
  cursor: pointer;
  background-color: #7dbb8abc;
  border: 0.4px solid #cac8c8;
  border-radius: 4px 4px 0 0;
  font-family: "Poppins", serif;
  font-weight: 200;
  font-style: normal;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #F2F2F0;
  border: 1px solid #e2e0e0;
  list-style: none;
  padding: 10px;
  margin: 0;
  display: none;
  /* Oculta o menu inicialmente */
  font-size: 13px;
  font-family: "Poppins", serif;
  font-weight: 200;
  font-style: normal;
}

.dropdown-menu li {
  padding: 5px 0;
}

.dropdown-menu li:hover {
  background-color: #f0f0f0;
}

span {
  color: #F26938;
  font-weight: 600;
}

/* Exibe o menu quando o estado estiver ativo */
.dropdown.open .dropdown-menu {
  display: block;
}

/* Responsividade */
@media (max-width: 481px) {
  .menugrupo-description {
    padding: 30px;
    padding-top: 10px;
    margin: 0;
  }

  .menugrupo-description p {
    font-size: 12px;
    line-height: 1.7;
  }

  .dropdown-button {
    padding: 10px 15px;
    font-size: 12px;
  }

  .dropdown-menu {
    position: absolute;
    padding: 2px;
    margin: 2;
    display: none;
    /* Oculta o menu inicialmente */
    font-size: 10px;
    font-family: "Poppins", serif;
    font-weight: 200;
    font-style: normal;
    text-align: center;
  }

  .menu-titulo_bloco2 {
    margin-top: 8px;
    margin-bottom: 20px;
  }

  .bloco2-p {
    font-size: 12px;
    line-height: 1.7;
    margin: 40px;
    text-align: justify;

  }

  .menu-titulo-button {
    padding: 15px;
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 20px;

  }
}

/* Smartphones pequenos (Portrait: até 768px) */
@media (min-width: 482px) and (max-width: 768px) {

  .menugrupo-description {
    padding: 30px;
    padding-top: 10px;
    margin: 20px;
  }

  .menugrupo-description p {
    font-size: 14px;
    line-height: 1.7;
  }

  .dropdown-button {
    padding: 10px 15px;
    font-size: 12px;
  }

  .dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #F2F2F0;
    border: 1px solid #e2e0e0;
    list-style: none;
    padding: 8px;
    margin: 0;
    display: none;
    /* Oculta o menu inicialmente */
    font-size: 13px;
    font-family: "Poppins", serif;
    font-weight: 200;
    font-style: normal;
  }

  .menu-titulo_bloco2 {
    margin-top: 8px;
    margin-bottom: 20px;
  }

  .bloco2-p {
    font-size: 14px;
    line-height: 1.7;
    margin: 40px;
    text-align: justify;

  }

  .menu-titulo-button {
    padding: 15px;
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 20px;

  }
}

/* Smartphones pequenos (Portrait: até 768px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .menugrupo-h1 {
    margin-top: 50px;
    margin-bottom: 50px;
  }

  .menugrupo-description p {
    font-size: 16px;
    line-height: 1.7;
  }

  .dropdown-button {
    padding: 10px 15px;
    font-size: 16px;

  }


  .bloco2-p {
    font-size: 16px;
    line-height: 1.7;
    margin: 40px;
    text-align: justify;

  }

  .menu-titulo-button {
    padding: 15px;
    font-size: 16px;
    margin-top: 10px;
    margin-bottom: 20px;

  }

  .dropdown-menu {

    font-size: 16px;

  }
}

@media (min-width: 1025px) {
  .menugrupo-h1 {
    margin-top: 60px;
    margin-bottom: 50px;
  }

  .menugrupo-description p {
    font-size: 20px;
    line-height: 1.7;
  }

  .dropdown-button {
    padding: 10px 15px;
    font-size: 22px;
  }


  .bloco2-p {
    font-size: 20px;
    line-height: 1.7;
    margin: 40px;
    padding: 25px;
    text-align: justify;
  }

  .menu-titulo-button {
    padding: 15px;
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 20px;
  }

  .dropdown-menu {
    font-size: 16px;
  }
}