/* Estilo geral da página */
.page-container {
    width: 100%;
    height: auto;
    background-image: url("../assets/reservas.jpg");
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: flex-end; 
    padding: 20px;
    position: relative;
  }
  
  /* Formulário */
  .form-container {
    background-color: #E5EAE6; 
    border-radius: 8px;
    padding: 20px;
    width: 400px;
    margin-right: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    margin-top: 40px;
    margin-bottom: 20px;
  }
  
  .form-container h1 {
    font-family: "Poppins", serif;
    font-weight: 500;
    font-size: 24px;
    margin-bottom: 3px;
    padding-bottom: 3px;
    margin-top: 5px;
    text-align: center;
  }
  
  .form-container p {
    font-weight: 300;
    font-size: 13px;
    text-align: center;
    margin-bottom: 20px;
    margin-top: 5px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-container input,
  .form-container textarea {
    width: calc(100% - 20px);
    padding: 10px;
    border-radius: 8px;
    border: 1px solid #ddd;
  }
  
  .form-container button {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    border-radius: 8px;
    border: none;
    background-color: #ffa07a;
    color: white;
    font-family: "Poppins", serif;
    font-weight: 500;
    cursor: pointer;
  }
  
  .form-container button:hover {
    background-color: #ff6347;
  }
  .error {
    color: red;
    font-size: 12px;
    margin-top: 5px;
  }
  
  /* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6); /* Fundo semi-transparente */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  text-align: center;
  width: 300px;
  animation: fadeIn 0.3s ease-in-out;
}

.modal-content h2 {
  font-family: "Poppins", serif;
  font-size: 20px;
  margin-bottom: 10px;
}

.modal-content p {
  font-family: "Poppins", serif;
  font-size: 14px;
  margin-bottom: 20px;
  color: #333;
}

.modal-content button {
  background-color: #ffa07a;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-family: "Poppins", serif;
  font-weight: 500;
}

.modal-content button:hover {
  background-color: #ff6347;
}

/* Animação para o modal */
@keyframes fadeIn {
  from {
      opacity: 0;
      transform: scale(0.9);
  }
  to {
      opacity: 1;
      transform: scale(1);
  }
}
